import React from 'react'
import Helmet from 'react-helmet'

const Seo = ({ title, metaDescription }) => {
  return <>
    <Helmet title={title} />
    <Helmet>
      <link rel="icon" href={'/src/images/icon.png'} />

      <meta property="og:locale" content ="en" />
      <meta property="og:site_name" content="GM Aesop's Fables" />
      <meta property="og:title" content={title} />
      <meta property="og:url" content="https://gmaesopsfables.org" />
      <meta property="og:description" content={metaDescription} />
      <meta property="og:image" content='/src/images/icon.png' />
      <meta property="og:type" content='website' />

      <meta name="twitter:card" content='/src/images/icon.png' />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={metaDescription} />
      <meta name="twitter:image" content='/src/images/icon.png' />
    </Helmet>
  </>
}

export default Seo

